import {
    BrowserRouter,
    Routes,
    Route,
    Switch,
} from "react-router-dom";
import './App.css';
import Home from "./pages/home/home";
import List from "./pages/list/List";
import Hostel from "./pages/hostel/hostel";
import HostelDetails from "./pages/hostels/hostels";
import Register from "./pages/register/register";
import AddToCart from "./pages/cart/cart";
import Signin from "./pages/signIn/signIn";
import SignUp from "./pages/signUp/signUp";
import Dashboard from "./pages/dashboard/dashboard";
import LandingPage from "./pages/landingPage/landingPage";
import Images from "./pages/Images/images";

function App() {
    return (
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/hostels" element={<List />} />
        <Route path="/hostels/:name" element={<Hostel />} />
        <Route path="/hostels-page" element={<HostelDetails />} />
        <Route path="/Register" element={<Register />} />
        <Route path="/cart/:name" element={<AddToCart />} />
        <Route path="/Signin" element={<Signin />} />
        <Route path="/SignUp" element={<SignUp />} />
    <Route path="/landing" element={<LandingPage />} />
        <Route path="/dashboard/:hostelName" element={<Dashboard />} />
         <Route path="/images" element={<Images />} />
      </Routes>
    </BrowserRouter>
  );
}


export default App;