// SignUp.jsx

import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import "./signUp.css";

const SignUp = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');
  const [isSignUpSuccess, setIsSignUpSuccess] = useState(false);

  const handleEmailChange = (e) => {
    setEmail(e.target.value);
  };

  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
  };

  const handleConfirmPasswordChange = (e) => {
    setConfirmPassword(e.target.value);
  };

  const handleSignUpClick = () => {
    if (password !== confirmPassword) {
      setError('Passwords do not match');
      return;
    }

    // Now you can proceed to save the data to the database
    // using the email and password values

    // Example fetch request (replace with your actual API endpoint)
    fetch('http://localhost:5000/signup', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        email,
        password,
      }),
    })
      .then((response) => {
        if (response.ok) {
          console.log('Sign up successful');
          setIsSignUpSuccess(true);
        } else {
          console.error('Sign up failed');
          setIsSignUpSuccess(false);
        }
      })
      .catch((error) => {
        console.error('Error in sign-up request:', error);
      });
  };

  return (
    <div>
      <h1>Sign Up</h1>
      <div>
        <label>Email:</label>
        <input type="email" value={email} onChange={handleEmailChange} />
      </div>
      <div>
        <label>Password:</label>
        <input type="password" value={password} onChange={handlePasswordChange} />
      </div>
      <div>
        <label>Confirm Password:</label>
        <input type="password" value={confirmPassword} onChange={handleConfirmPasswordChange} />
      </div>
      {error && <p style={{ color: 'red' }}>{error}</p>}
      <button onClick={handleSignUpClick}>Sign Up</button>

      {isSignUpSuccess && (
        <p>
          Sign up successful! <Link to="/register">Enter hostel information</Link>.
        </p>
      )}
    </div>
  );
};

export default SignUp;
