// dashboard.jsx

import React, { useState, useEffect } from 'react';
import './dashboard.css';
import { useParams } from 'react-router-dom';

const Dashboard = () => {
  const { hostelName } = useParams();
  const [hostelData, setHostelData] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch(`http://localhost:5000/dashboard/${hostelName}`);
        if (response.ok) {
          const data = await response.json();
          setHostelData(data);
        } else {
          console.error('Error fetching dashboard data:', response.statusText);
        }
      } catch (error) {
        console.error('Error fetching dashboard data:', error.message);
      }
    };

    fetchData();
  }, [hostelName]);

  return (
    <div className="dashboard">
      {hostelData && (
        <>
          <h1>{hostelName}</h1>
          <table>
            <thead>
              <tr>
                <th>Registration Number</th>
                <th>Email</th>
                <th>Contact</th>
                <th>Room Type</th>
              </tr>
            </thead>
            <tbody>
              {hostelData.map((item) => (
                <tr key={item._id}>
                  <td>{item.regno}</td>
                  <td>{item.email}</td>
                  <td>{item.contact}</td>
                  <td>{item.roomType}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </>
      )}
    </div>
  );
};

export default Dashboard;
