// LandingPage.jsx

import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';

const LandingPage = () => {
  const [hostelNames, setHostelNames] = useState([]);
  const [selectedHostel, setSelectedHostel] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    const fetchHostelNames = async () => {
      try {
        const response = await fetch('http://localhost:5000/dashboard');
        if (response.ok) {
          const data = await response.json();
          const names = Array.from(new Set(data.map((item) => item.hostelName)));
          setHostelNames(names);
        } else {
          console.error('Error fetching hostel names:', response.statusText);
        }
      } catch (error) {
        console.error('Error fetching hostel names:', error.message);
      }
    };

    fetchHostelNames();
  }, []);

  const handleSearch = () => {
    if (selectedHostel) {
      navigate(`/dashboard/${selectedHostel}`);
    } else {
      alert('Please select a hostel before searching.');
    }
  };

  return (
    <div className="landing-page">
      <h1>Hostel Search</h1>
      <label htmlFor="hostelDropdown">Select Hostel: </label>
      <select
        id="hostelDropdown"
        onChange={(e) => setSelectedHostel(e.target.value)}
        value={selectedHostel}
      >
        <option value="" disabled>Select a Hostel</option>
        {hostelNames.map((name) => (
          <option key={name} value={name}>
            {name}
          </option>
        ))}
      </select>

      <button onClick={handleSearch}>Search</button>

      <p>
        View details for{' '}
        {selectedHostel && (
          <Link to={`/dashboard/${selectedHostel}`}>{selectedHostel}</Link>
        )}
        {' '}here.
      </p>
    </div>
  );
};

export default LandingPage;
