// list.jsx
import './list.css';
import Header from "../../components/header/Header";
import Navbar from "../../components/navbar/navbar";
import { useLocation } from 'react-router-dom';
import { useState, useEffect } from 'react';
import SearchItem from '../../components/searchItem/SearchItem';

const List = () => {
    const location = useLocation();
    const initialDestination = location.state?.destination || '';
    const [destination, setDestination] = useState(initialDestination);
    const [hostels, setHostels] = useState([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchHostels = async () => {
            try {
                const response = await fetch("http://localhost:5000/hostels");
                if (response.ok) {
                    const hostelData = await response.json();
                    setHostels(hostelData);
                } else {
                    console.error('Error fetching hostels:', response.statusText);
                }
            } catch (error) {
                console.error('Error fetching hostels:', error.message);
            } finally {
                setLoading(false);
            }
        };

        fetchHostels();
    }, []);

    return (
        <div>
            <Navbar />
            <Header type="List" />
            <div className="listContainer">
                <div className="listWrapper">
                    <div className="listSearch">
                        <h1 className="lsTitle">Search</h1>
                        <div className="lsItem">
                            <label>Destination</label>
                            <input placeholder={destination} type='text' />
                        </div>0
                        {/* ... rest of your search component */}
                    </div>
                  <div className="listResult">
                {loading ? (
                    <p>Loading...</p>
                ) : (
                    hostels.map((hostel, index) => {
                        const imageUrl = hostel.hostelImages && hostel.hostelImages.length > 0
                            ? `/${encodeURIComponent(hostel.hostelImages[0])}`
                            : null;

                        console.log(`Image URL: ${imageUrl}`);

                        return (
                            <SearchItem
                                key={index}
                                name={hostel.hostelName}
                                location={hostel.hostelLocation}
                                image={imageUrl}
                                bookingFee={hostel.bookingFee}
                                distance={hostel.distanceFromUniversity}
                            />
                        );
                    })
                )}
            </div>
</div>
            </div>
        </div>
    );
}

export default List;
