import { faBed } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { useNavigate } from "react-router-dom";  // Use useNavigate instead of useHistory
import "./Header.css";
import { useState } from "react";

const Header = ({ type }) => {
  const [hostelName, setHostelName] = useState('');
  const navigate = useNavigate();  // Use useNavigate instead of useHistory

  const handleSearch = async () => {
  try {
    const response = await fetch(`http://localhost:5000/hostels/${encodeURIComponent(hostelName)}`);
    if (response.ok) {
      const hostelsData = await response.json();
      console.log('Hostels Data:', hostelsData);

      if (Array.isArray(hostelsData)) {
        // Assuming you want to navigate to the first hostel in the array
        if (hostelsData.length > 0) {
          const firstHostel = hostelsData[0];
          navigate(`/hostels/${encodeURIComponent(firstHostel.hostelName)}`);
        } else {
          console.error('Hostel not found');
        }
      } else if (hostelsData && typeof hostelsData === 'object') {
        // Handle the case where hostelsData is an object
        navigate(`/hostels/${encodeURIComponent(hostelsData.hostelName)}`);
      } else {
        console.error('Invalid response format');
      }
    } else {
      console.error('Failed to fetch hostels');
    }
  } catch (error) {
    console.error('Error fetching hostels:', error);
  }
};



  return (
    <div className="header">
      <div className={type === 'list' ? "headerContainer listMode" : "headerContainer"}>
        <div className="headerList">
          <div className="headerListItem">
            <FontAwesomeIcon icon={faBed} />
            <span>Stays</span>
          </div>
        </div>
        {type !== "List" && (
          <>
            <h1 className="headerTitle">A lifetime of discounts? It's Genius</h1>
            <p className="headerDesc">Get rewarded for your hostel bookings with 10% off when booked before 2 weeks</p>

            <div className="headerSearch">
              <div className="headerSearchItem">
                <FontAwesomeIcon icon={faBed} className="headerIcon" />
                <input
                  type="text"
                  placeholder="Where do you want to stay?"
                  className="headerSearchInput"
                  value={hostelName}
                  onChange={(e) => setHostelName(e.target.value)}
                />
              </div>
              <div className="headerSearchItem">
                <button className="headerBtn" onClick={handleSearch}>
                  Search
                </button>
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  );
}

export default Header;
