import React, { useState } from "react";
import { Button, Upload } from "antd";
import axios from "axios";

function Images() {
  const [fileList, setFileList] = useState([]);

  const handleUpload = async () => {
    const formData = new FormData();

    fileList.forEach((file) => {
      formData.append("files", file.originFileObj);
    });

    try {
      const response = await axios.post("http://localhost:5000/upload", formData);

      if (response.status === 200) {
        // Optionally, you can reset the fileList state after successful upload
        setFileList([]);
        console.log("Images uploaded successfully!");
      } else {
        console.error("Failed to upload images. Server returned status:", response.status);
      }
    } catch (error) {
      console.error("Error uploading images:", error.message);
    }
  };

  return (
    <div>
      <Upload
        beforeUpload={() => false} // Disable automatic upload
        onChange={({ fileList }) => setFileList(fileList)} // Update the fileList state
        fileList={fileList}
        multiple
      >
        <Button>Select Images</Button>
      </Upload>
      <Button onClick={handleUpload}>Upload</Button>
    </div>
  );
}

export default Images;
