import React, { useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import './cart.css';

const Cart = () => {
  const { name } = useParams();
  const navigate = useNavigate();
  const [roomType, setRoomType] = useState('single');
  const [contact, setContact] = useState('');
  const [email, setEmail] = useState('');
  const [regno, setRegno] = useState('');

  const handleRoomTypeChange = (e) => {
    setRoomType(e.target.value);
  };

  const handleContactChange = (e) => {
    setContact(e.target.value);
  };

  const handleRegnoChange = (e) => {
    setRegno(e.target.value);
  };

  const handleEmailChange = (e) => {
    setEmail(e.target.value);
  };

  const hostelDetails = {
    name,
    bookingFee: 500000,
  };

  const handleBookNowClick = async () => {
    try {
      const response = await fetch('http://localhost:5000/cart', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          hostelName: hostelDetails.name,
          regno,
          email,
          contact,
          roomType,
          bookingFee: hostelDetails.bookingFee,
        }),
      });

      if (response.ok) {
        console.log('Booking successful!');
        navigate('/');
      } else {
        console.error('Booking failed:', response.statusText);
      }
    } catch (error) {
      console.error('Error during booking:', error.message);
    }
  };

  return (
    <div className="Checkout">
      <h1>Cart</h1>
      <div className="hostel-details">
        <h2>{hostelDetails.name}</h2>
        <h4>Booking Fee</h4>
        <p>{hostelDetails.bookingFee} UGX per night</p>

        <div>
          <label>Registration Number:</label>
          <input type="text" value={regno} onChange={handleRegnoChange} />
        </div>
        <div>
          <label>Contact:</label>
          <input type="text" value={contact} onChange={handleContactChange} />
        </div>
        <div>
          <label>Email:</label>
          <input type="email" value={email} onChange={handleEmailChange} />
        </div>

        <div>
          <label>
            <input
              type="radio"
              value="single"
              checked={roomType === 'single'}
              onChange={handleRoomTypeChange}
            />
            Single Room
          </label>
        </div>
        <div>
          <label>
            <input
              type="radio"
              value="double"
              checked={roomType === 'double'}
              onChange={handleRoomTypeChange}
            />
            Double Room
          </label>
        </div>
        <div>
          <label>
            <input
              type="radio"
              value="triple"
              checked={roomType === 'triple'}
              onChange={handleRoomTypeChange}
            />
            Triple Room
          </label>
        </div>

        <button onClick={handleBookNowClick}>Book Now</button><br />
          
        <button onClick={() => navigate('/')}>Go to Homepage</button>
      </div>
    </div>
  );
};

export default Cart;
