import React, { useState, useEffect } from "react";
import { useParams, useHistory } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useNavigate } from 'react-router-dom';
import {
  faCircleArrowLeft,
  faCircleArrowRight,
  faCircleXmark,
  faLocationDot,
} from "@fortawesome/free-solid-svg-icons";
import Header from "../../components/header/Header";
import Navbar from "../../components/navbar/navbar";
import MailList from "../../components/mailList/MailList";
import Footer from "../../components/footer/Footer";
import "./hostel.css";

const Hostel = () => {
  const { name } = useParams();
  const [hostel, setHostel] = useState({});
  const [slideNumber, setSlideNumber] = useState(0);
  const [open, setOpen] = useState(false);

  // Use the correct hook for programmatic navigation
  const navigate = useNavigate();

  useEffect(() => {
   const fetchHostelData = async () => {
  try {
    const response = await fetch(`http://localhost:5000/hostels/${name}`);
    if (response.ok) {
      const hostelData = await response.json();
      console.log('Hostel Data:', hostelData); // Log the response
      setHostel(hostelData);
    } else {
      console.error("Error fetching hostel details:", response.statusText);
    }
  } catch (error) {
    console.error("Error fetching hostel details:", error.message);
  }
};

    fetchHostelData();
  }, [name]);

  const photos = hostel.hostelImages || [];

  const handleOpen = (i) => {
    setSlideNumber(i);
    setOpen(true);
  };

  const handleMove = (direction) => {
    let newSlideNumber;

    if (direction === "l") {
      newSlideNumber = slideNumber === 0 ? photos.length - 1 : slideNumber - 1;
    } else {
      newSlideNumber =
        slideNumber === photos.length - 1 ? 0 : slideNumber + 1;
    }
    setSlideNumber(newSlideNumber);
  };

  const handleReserveClick = () => {
    // Use the navigate function to redirect to the cart page
    navigate(`/cart/${name}`);
  };
  return (
    <div>
      <Navbar />
      <Header type="List" />
      <div className="hotelContainer">
        {open && (
          <div className="slider">
            <FontAwesomeIcon
              icon={faCircleXmark}
              className="close"
              onClick={() => setOpen(false)}
            />
            <FontAwesomeIcon
              icon={faCircleArrowLeft}
              className="arrow"
              onClick={() => handleMove("l")}
            />
            <div className="sliderWrapper">
              <img
                src={photos[slideNumber]?.data || ""}
                alt=""
                className="sliderImg"
              />
            </div>
            <FontAwesomeIcon
              icon={faCircleArrowRight}
              className="arrow"
              onClick={() => handleMove("r")}
            />
          </div>
        )}
        <div className="hotelwrapper">
          <button className="bookNow" onClick={handleReserveClick}>
            Reserve or Book now!
          </button>
          <h1 className="hotelTitle">{hostel.hostelName || ""}</h1>
          <div className="hotelAddress">
            <FontAwesomeIcon icon={faLocationDot} />
            <span>{hostel.hostelLocation || ""}</span>
          </div>
          <span className="hotelDistance">
            Excellent location - {hostel.distanceFromUniversity || ""}
          </span>
          <span className="hotelPriceHighlight">
            Book a stay here and get 10% off
          </span>
         <div className="hotelImages">
        {photos.map((photo, i) => (
          <div className="hotelImgWrapper" key={i}>
            <img
  onClick={() => handleOpen(i)}
  src={`/uploads/${photo}`}
  alt={`Hostel ${name} Image ${i}`}
  className="hotelImg"
/>
          </div>
        ))}
      </div>
          <div className="hotelDetails">
            <div className="hotelDetailsTexts">
              <h1 className="hotelTitle">{hostel.roomType || ""}</h1>
              <p className="hotelDesc">{hostel.hostelDescription || ""}</p>
              <p className="hotelDesc">
                Facilities: {hostel.hostelFacilities?.join(", ") || ""}
              </p>
            </div>
            <div className="hotelDetailsPrice">
              <h1>Perfect for your whole semester stay</h1>
              <span>
                located in {hostel.hostelLocation || ""}, this hostel has an
                excellent score of {hostel.rating || 0}!
              </span>
              <h2>
                <b>UGX {hostel.bookingFee || ""}</b> (booking fee)
              </h2>
              <button onClick={handleReserveClick}>Reserve or Book now!</button>
            </div>
          </div>
        </div>
        <MailList />
        <Footer />
      </div>
    </div>
  );
};

export default Hostel;
