import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCircleArrowLeft, faCircleArrowRight, faCircleXmark, faLocationDot } from "@fortawesome/free-solid-svg-icons";

import './hostels.css'; // Import the CSS file for styling

const HostelDetails = () => {
  const [hostel, setHostel] = useState(null);
  const [slideNumber, setSlideNumber] = useState(0);

  const { hostelName } = useParams();

 useEffect(() => {
  const fetchHostelDetails = async () => {
    try {
      const response = await axios.get(`http://localhost:5000/api/hostels/hostels/${hostelName}`);
      if (response.ok) {
        const hostelData = response.data;
        console.log('Hostel Data:', hostelData); // Log the hostel data
          console.log(`Image URL: http://localhost:5000/uploads/${hostel.hostelImages[slideNumber]}`);

        setHostel(hostelData);
      } else {
        console.error('Error fetching hostel details:', response.statusText);
      }
    } catch (error) {
      console.error('Error fetching hostel details:', error.message);
    }
  };

  fetchHostelDetails();
}, [hostelName]);

  const handleMove = (direction) => {
    let newSlideNumber;

    if (direction === "l") {
      newSlideNumber = slideNumber === 0 ? hostel.hostelImages.length - 1 : slideNumber - 1;
    } else {
      newSlideNumber = slideNumber === hostel.hostelImages.length - 1 ? 0 : slideNumber + 1;
    }
    setSlideNumber(newSlideNumber);
  };

  return (
    <div className="hostel-details-container">
      {hostel && (
        <div className="hostel-details">
          {/* ... (your existing code) */}
          <div className="slider">
            <FontAwesomeIcon icon={faCircleXmark} className="close" />
            <FontAwesomeIcon icon={faCircleArrowLeft} className="arrow" onClick={() => handleMove("l")} />
            <div className="sliderWrapper">
              <img src={`http://localhost:5000/uploads/${hostel.hostelImages[slideNumber]}`} alt="" className="sliderImg" />
            </div>
            <FontAwesomeIcon icon={faCircleArrowRight} className="arrow" onClick={() => handleMove("r")} />
          </div>
          {/* ... (rest of your component) */}
        </div>
      )}
    </div>
  );
};

export default HostelDetails;