import React, { useState } from 'react';
import axios from 'axios';

const Register = () => {
  const [formData, setFormData] = useState({
    hostelName: '',
    hostelLocation: '',
    custodianContact: '',
    distanceFromUniversity: '',
    hostelPrice: '',
    hostelDescription: '',
    hostelRooms: '',
    hostelFacilities: '',
    bookingFee: '',
    hostelImages: null,
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleImageChange = (e) => {
    setFormData((prevData) => ({
      ...prevData,
      hostelImages: e.target.files,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const formDataToSend = new FormData();
      for (const key in formData) {
        if (key === 'hostelImages') {
          for (let i = 0; i < formData[key].length; i++) {
            formDataToSend.append(key, formData[key][i]);
          }
        } else {
          formDataToSend.append(key, formData[key]);
        }
      }

      await axios.post('http://localhost:5000/api/hostels/addhostel', formDataToSend, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });

      alert('Hostel registered successfully!');
    } catch (error) {
      console.error('Error registering hostel:', error);
      alert('Error registering hostel. Please try again.');
    }
  };

  return (
    <div>
      <h1>Hostel Registration</h1>
      <form onSubmit={handleSubmit}>
        {/* Add your input fields here */}
        <label>
          Hostel Name:
          <input type="text" name="hostelName" value={formData.hostelName} onChange={handleChange} />
        </label>
         <label>
          Hostel Location:
          <input type="text" name="hostelLocation" value={formData.hostelLocation} onChange={handleChange} />
        </label>
         <label>
          Custodian contact:
          <input type="text" name="custodianContact" value={formData.custodianContact} onChange={handleChange} />
        </label>
         <label>
          Distance From University:
          <input type="text" name="distanceFromUniversity" value={formData.distanceFromUniversity} onChange={handleChange} />
        </label>
         <label>
          Hostel Price:
          <input type="text" name="hostelPrice" value={formData.hostelPrice} onChange={handleChange} />
        </label>
         <label>
          Hostel Description:
          <input type="text" name="hostelDescription" value={formData.hostelDescription} onChange={handleChange} />
        </label>
         <label>
          Hostel Rooms:
          <input type="text" name="hostelRooms" value={formData.hostelRooms} onChange={handleChange} />
        </label>
         <label>
          Hostel Facilities:
          <input type="text" name="hostelFacilities" value={formData.hostelFacilities} onChange={handleChange} />
        </label>
         <label>
          Booking Fee:
          <input type="number" name="bookingFee" value={formData.bookingFee} onChange={handleChange} />
        </label>

        {/* Repeat similar lines for other fields */}

        <label>
          Hostel Images:
          <input type="file" name="hostelImages" multiple onChange={handleImageChange} />
        </label>
        

        <button type="submit">Register Hostel</button>
      </form>
    </div>
  );
};

export default Register;
