// SearchItem.jsx
import React from 'react';
import { Link } from 'react-router-dom';
import './searchItem.css';

const SearchItem = ({ name, location, image, bookingFee, distance }) => {
  const imageUrl = image ? `http://localhost:5000/uploads/${encodeURIComponent(image)}` : ''; // Check if image is available

  return (
    <Link to={`/hostels/${encodeURIComponent(name)}`} className="search-item">
      <div className="siImage">
        {imageUrl && <img src={imageUrl} alt={name} />}
      </div>
      <div className="siDetails">
        <h2>{name}</h2>
        <p>{location}</p>
        <p>Booking Fee: {bookingFee} UGX</p>
        <p>Distance: {distance}</p>
      </div>
    </Link>
  );
};

export default SearchItem;
